.container {
  max-width: 800px;
  padding: 20px;
}

.content {
  display: flex;
  margin-top: 20px;
}

.image {
  width: 300px;
  height: 450px;
  object-fit: cover;
  margin-right: 20px;
}

.info {
  margin-left: 30px;
}

h1 {
  font-size: 32px;
  margin-bottom: 10px;
}

p {
  font-size: 16px;
  margin-bottom: 10px;
}

.additional {
  margin-left: 30px;
}

.additional p {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.additional ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.additional li {
  font-size: 16px;
  margin-bottom: 5px;
}

.additional li a {
  color: #0075ff;
  text-decoration: none;
}

.additional li a:hover {
  text-decoration: underline;
}

.button {
  padding: 6px 8px;
  font-size: 14px;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: #0062cc;
}

.link {
  text-decoration: none;
  color: #fff;
}
